import React from 'react'
import { Fab } from '@material-ui/core'
import LayersClearIcon from '@material-ui/icons/LayersClear'
import LayersIcon from '@material-ui/icons/Layers'

interface Props {
    drawerOpen?: boolean
    toggleDrawer: () => void
}

const ProjectLoading: React.FC<Props> = ({ drawerOpen, toggleDrawer }: Props) => {
    return (
        <Fab color="primary" aria-label="add" onClick={toggleDrawer} size="small">
            {drawerOpen ? <LayersClearIcon /> : <LayersIcon />}
        </Fab>
    )
}

export default ProjectLoading
